.hero-sec {
  background: linear-gradient(hsl(252, 100%, 67%), hsl(241, 81%, 54%));
  display: flex;
  flex-direction: column;
}
.hero-sec h1 {
  color: hsl(241, 100%, 89%);
}
.hero-sec h2 {
  color: hsl(0, 0%, 100%);
  background: linear-gradient(hsl(256, 72%, 46%), hsla(241, 72%, 46%, 0));
  min-width: 7.4138888889rem;
  min-height: 7.1444444444rem;
  align-self: center;
  border-radius: 50%;
  padding: 1.5rem;
}
.hero-sec h2 span {
  color: hsla(241, 100%, 89%, 0.5);
}
.hero-sec h3 {
  color: hsl(0, 0%, 100%);
}
.hero-sec p {
  color: hsl(241, 100%, 89%);
}

.summary-sec h3 {
  color: hsl(224, 30%, 27%);
}
.summary-sec article:nth-of-type(1) {
  background-color: hsla(0, 100%, 67%, 0.104);
}
.summary-sec article:nth-of-type(1) h4:nth-child(1) {
  color: hsl(0, 100%, 67%);
}
.summary-sec article:nth-of-type(2) {
  background-color: hsla(39, 100%, 56%, 0.104);
}
.summary-sec article:nth-of-type(2) h4:nth-child(1) {
  color: hsl(39, 100%, 56%);
}
.summary-sec article:nth-of-type(3) {
  background-color: hsla(166, 100%, 37%, 0.104);
}
.summary-sec article:nth-of-type(3) h4:nth-child(1) {
  color: hsl(166, 100%, 37%);
}
.summary-sec article:nth-of-type(4) {
  background-color: hsla(234, 85%, 45%, 0.104);
  margin-bottom: 2rem;
}
.summary-sec article:nth-of-type(4) h4:nth-child(1) {
  color: hsl(234, 85%, 45%);
}
.summary-sec h4:nth-child(2) {
  color: hsl(241, 100%, 89%);
}
.summary-sec strong {
  color: hsl(224, 30%, 27%);
}

button {
  background-color: hsl(224, 30%, 27%);
  color: hsl(0, 0%, 100%);
  transition: all 0.5s ease;
}

button:hover {
  background: linear-gradient(hsl(252, 100%, 67%), hsl(241, 81%, 54%));
  cursor: pointer;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

@font-face {
  font-family: "hank";
  src: url(../../../assets/fonts/hankengrotesk-variablefont_wght-webfont.woff), url(../../../assets/fonts/hankengrotesk-variablefont_wght-webfont.woff2);
  font-weight: normal;
  font-style: normal;
}
html {
  font-family: "hank";
  font-size: 18px;
}

.hero-sec {
  padding: 2rem 3rem;
}
.hero-sec h1 {
  font-size: 0.9rem;
  margin-bottom: 1rem;
}
.hero-sec h2 {
  font-size: 2.3rem;
  font-weight: 800;
  margin-bottom: 1.3rem;
}
.hero-sec h2 span {
  font-size: 0.7rem;
  font-weight: 500;
  display: block;
}
.hero-sec h3 {
  margin-bottom: 0.8rem;
}
.hero-sec p {
  font-size: 0.8rem;
}

.summary-sec {
  padding: 1.4rem;
}
.summary-sec h3 {
  text-align: left;
  font-size: 1rem;
}
.summary-sec article {
  display: flex;
  justify-content: space-between;
  padding: 0.6rem;
  border-radius: 0.4rem;
  margin-bottom: 1rem;
  margin-top: 0.3rem;
}
.summary-sec article h4 {
  font-size: 0.9rem;
  font-weight: 700;
}
.summary-sec img {
  margin-bottom: -0.2rem;
  margin-right: 0.4rem;
}

button {
  font-weight: 800;
}

.attribution {
  font-size: 0.6111111111rem;
  text-align: center;
}

@media only screen and (min-width: 700px) {
  html {
    font-size: 19px;
  }
}
*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  position: relative;
  display: grid;
  place-items: center;
}

main {
  min-height: 100vh;
  text-align: center;
}
main .hero-sec {
  border-bottom-left-radius: 1.7rem;
  border-bottom-right-radius: 1.7rem;
}
main .summary-sec h3 {
  margin-bottom: 1rem;
}
main button {
  width: 100%;
  font-family: inherit;
  padding: 0.8rem;
  border-radius: 2rem;
  border: none;
}

footer {
  position: absolute;
  bottom: 0;
}

@media only screen and (min-width: 700px) {
  main {
    display: grid;
    grid-template-columns: repeat(2, minmax(auto, 19.4444444444rem));
    max-width: 38.8888888889rem;
    min-height: 23.6111111111rem;
    width: 52%;
    overflow: hidden;
    border-radius: 1.7rem;
    box-shadow: 5px 4px 15px 2px rgba(0, 0, 0, 0.2);
  }
  main .hero-sec {
    border-top-right-radius: 1.7rem;
    padding: 3.2rem;
    width: 95%;
  }
  main .hero-sec h1 {
    margin-bottom: 2rem;
    margin-top: 0.7rem;
  }
  main .hero-sec h2 {
    margin-bottom: 1.7rem;
  }
  main .summary-sec h3 {
    margin-top: 1rem;
    margin-bottom: 1.5rem;
  }
}